.s-contacts {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: rem(48) 0 rem(82);
    // background-image: url(/img/back/contacts.png);
    background-repeat: no-repeat;
    background-position: 0 rem(-100);
    background-size: cover;

    @media (min-width: 768px) {
        padding: rem(40) 0 rem(85);
    }

    @media (min-width: 1024px) {
        padding: rem(93) 0 rem(97);
    }

    @media (min-width: 1600px) {
        padding: rem(146) 0 rem(152);
        background-position-x: center;
        background-position-y: rem(-120);
        background-size: contain;
    }

    @media (min-width: 1920px) {
        padding: rem(150) 0 rem(157);
    }

    &__inner {
        max-width: rem(995);
        margin: 0 auto;

        @media (min-width: 1920px) {
            max-width: 100%;
        }
    }

    &__tabs {
        display: flex;
        justify-content: space-between;
        margin-bottom: rem(10);
        flex-wrap: wrap;

        @media (min-width: 768px) {
            margin-bottom: rem(20);
            flex-wrap: nowrap;
        }

        @media (min-width: 1440px) {
            margin-bottom: rem(20);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(29);
        }
    }

    &__stock {
        display: flex;
        align-items: center;
        margin-top: rem(5);
        font-size: 9px;
        line-height: 110%;
        color: #4e5056;

        @media (min-width: 768px) {
            display: none;
        }
    }

    &__item {
        width: 48%;
        text-align: left;
        &:hover{
            .s-contacts__item-title{
                border-bottom: rem(2) solid $red;
            }
            .s-contacts__item-prefix {
                @media (min-width: 768px) {
                    span{
                        opacity: 0.7;
                    }
                }
                &::before {
                    background: $red;
                }
            }
        }

        @media (min-width: 768px) {
            &.--office {
                width: rem(246);
            }

            &.--stock {
                width: rem(390);
            }
        }

        @media (min-width: 1024px) {
            &.--office {
                width: rem(300);
            }

            &.--stock {
                width: rem(500);
            }
        }

        @media (min-width: 1600px) {
            &.--stock {
                width: rem(600);
            }
        }

        @media (min-width: 1920px) {
            &.--stock {
                width: rem(840);
            }
        }

        &.is-active {
            .s-contacts__item-title{
                border-bottom: rem(2) solid $red;
            }
            .s-contacts__item-prefix {
                &::before {
                    background: $red;
                }
                span {
                    opacity: 1 !important;
                }
            }
        }

        &-title {
            position: relative;
            width: 100%;
            margin-bottom: rem(20);
            padding-bottom: rem(5);
            font-size: rem(21);
            font-weight: 300;
            line-height: 1.2;
            color: $grey_light_more_2;
            transition: .3s;

            border-bottom: rem(2) solid #fff;
            @media (min-width: 768px) {
                max-width: rem(247);
                padding-bottom: rem(10);
                font-size: rem(30);
                font-weight: 400;
                line-height: rem(35);
            }
            @media (min-width: 1024px) {
                max-width: rem(300);
                font-size: rem(40);
                font-weight: 300;
                line-height: rem(47);
            }
            @media (min-width: 1600px) {
                font-size: rem(50);
                line-height: rem(59);
            }
            @media (min-width: 1920px) {
                max-width: rem(350);
                margin-bottom: rem(12);
                padding-bottom: rem(18);

                font-size: rem(60);
                line-height: rem(70);
            }
        }

        &::after {
            display: block;
            width: rem(2);
            height: rem(20);
            margin-bottom: rem(2);
            background: $red;
            content: "";
            @media (min-width: 768px) {
                display: none;
            }
        }

        &-prefix {
            position: relative;
            display: none;
            min-height: rem(60);
            padding-left: rem(10);
            font-size: rem(10);
            font-weight: 400;
            line-height: 120%;
            color: $grey;
            transition: 0.5s;

            @media (min-width: 768px) {
                display: block;
                span {
                    transition: .3s;
                    opacity: 0;
                }
            }

            @media (min-width: 1024px) {
                padding-left: rem(13);
                font-size: rem(11);
            }
            @media (min-width: 1600px) {
                padding-left: rem(15);
                font-size: rem(12);
            }
            @media (min-width: 1920px) {
                padding-left: rem(35);
                font-size: rem(15);
            }
            &::before {
                position: absolute;
                top: 50%;
                left: 0;
                display: block;
                width: rem(2);
                height: rem(20);
                background: #03030300;
                transform: translateY(-50%);
                transition: .3s;
                content: "";
                @media (min-width: 1600px) {
                    top: 45%;
                }
                @media (min-width: 1920px) {
                    top: 50%;
                    height: rem(25);
                }
            }
        }
    }
    &__box {
        position: relative;
        display: none;
        margin-bottom: rem(10);
        padding: rem(25) rem(30);
        border: 1px solid $white;

        @media (min-width: 768px) {
            padding: rem(40) rem(73);
        }

        @media (min-width: 1024px) {
            padding: rem(50) rem(45);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(20);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(30);
            padding: rem(90) rem(53);
        }

        &.is-active {
            display: block;
        }
        &-container {
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            margin-bottom: rem(10);

            &:last-child {
                margin: 0;
            }
            &:hover {
                .s-contacts__box-text {
                    color: $grey_light_more_2;
                }
            }
        }
        &-subtitle {
            min-width: rem(64);
            font-size: rem(15);
            font-weight: 300;
            line-height: 1.4;
            text-align: right;

            color: $grey_light;
            @media (min-width: 768px) {
                min-width: rem(90);
                font-size: rem(21);
                font-weight: 300;
            }
            @media (min-width: 1024px) {
                min-width: rem(120);
                font-size: rem(28);
            }
            @media (min-width: 1600px) {
                min-width: rem(135);
                font-size: rem(32);
            }
            @media (min-width: 1920px) {
                min-width: rem(170);
                font-size: rem(40);
            }
        }
        &-text {
            margin-left: rem(18);
            font-size: rem(15);
            font-weight: 400;
            line-height: 1;
            color: #9a9ea9;
            letter-spacing: -0.03em;
            @media (min-width: 768px) {
                margin-left: rem(30);
                font-size: rem(21);
            }
            @media (min-width: 1024px) {
                margin-left: rem(30);
                font-size: rem(28);
            }
            @media (min-width: 1600px) {
                margin-left: rem(22);
                font-size: rem(32);
            }
            @media (min-width: 1920px) {
                margin-left: rem(40);
                font-size: rem(40);
            }
        }
        &-popup {
            position: absolute;
            right: rem(10);
            bottom: rem(10);
            font-size: rem(8);
            font-weight: 400;
            line-height: 170%;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                right: rem(30);
                bottom: rem(30);
                font-size: rem(10);
            }

            @media (min-width: 1024px) {
                font-size: rem(13);
            }

            @media (min-width: 1920px) {
                right: rem(30);
                bottom: rem(30);
                font-size: rem(15);
            }
        }
    }
    &__information {
        display: block;
        margin-bottom: rem(56);
        gap: rem(10);

        @media (min-width: 768px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        @media (min-width: 1024px) {
            margin-bottom: rem(90);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(118);
        }

        @media (min-width: 1920px) {
            justify-content: space-between;
        }

        &-item {
            display: inline-flex;
            margin-right: rem(17);
            font-size: rem(10);
            font-weight: 300;
            line-height: 1.2;
            color: #6b7081;
            white-space: nowrap;

            @media (min-width: 1024px) {
                font-size: rem(12);
            }
            @media (min-width: 1600px) {
                font-size: rem(13);
            }
            span {
                margin-right: rem(5);
                font-weight: 400;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    &__driving {
        @media (min-width: 768px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
        }
        @media (min-width: 1920px) {
            max-width: rem(995);
        }
        &-title {
            display: inline-block;
            margin-bottom: rem(5);
            padding-bottom: rem(5);
            font-size: rem(21);
            font-weight: 400;
            line-height: rem(25);
            color: $grey_light_more_2;
            border-bottom: rem(2) solid $red;

            @media (min-width: 768px) {
                max-width: rem(287);
                padding-bottom: rem(10);
                font-size: rem(30);
                font-weight: 400;
                line-height: rem(35);
            }
            @media (min-width: 1024px) {
                padding-bottom: rem(21);
                font-size: rem(30);
            }
            @media (min-width: 1600px) {
                padding-bottom: rem(20);
                font-size: rem(40);
                line-height: rem(47);
            }
        }
        &-prefix {
            margin-bottom: rem(20);
            font-size: rem(9);
            font-weight: 400;
            line-height: rem(11);

            color: $grey_light_more_2;
            @media (min-width: 768px) {
                max-width: rem(340);
                margin-bottom: rem(0);
            }
            @media (min-width: 1024px) {
                max-width: rem(460);
                margin-right: auto;
                margin-left: rem(88);
                font-size: rem(13);
                line-height: rem(15);
            }
            @media (min-width: 1600px) {
                margin-left: rem(87);
            }
            @media (min-width: 1920px) {
                max-width: rem(560);
                margin-right: 0;
                margin-left: rem(0);
                font-size: rem(15);
            }
        }
        &-location {
            width: 100%;
            margin-top: rem(25);

            @media (min-width: 768px) {
                margin-top: rem(35);
            }
            @media (min-width: 1024px) {
                margin-top: rem(53);
            }
            @media (min-width: 1920px) {
                margin-top: rem(50);
            }
        }
    }
    &__location {
        &-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: rem(23);
            @media (min-width: 768px) {
                justify-content: left;
                margin-bottom: rem(20);
            }
            @media (min-width: 1024px) {
                justify-content: space-between;
                margin-bottom: rem(33);
            }
            @media (min-width: 1024px) {
                margin-bottom: rem(40);
            }
            @media (min-width: 1920px) {
                margin-bottom: rem(37);
            }
        }
        &-title {
            font-size: rem(15);
            font-weight: 400;
            line-height: rem(18);
            color: #9a9ea9;
            @media (min-width: 768px) {
                min-width: rem(273);
                font-size: rem(17);
                line-height: rem(20);
            }
            @media (min-width: 1600px) {
                font-size: rem(19);
                line-height: rem(22);
            }
        }
        &-btn {
            display: flex;
            align-items: center;
            @media (min-width: 768px) {
                margin-left: rem(97);
            }
            @media (min-width: 1024px) {
                margin-right: auto;
                margin-left: rem(375);
            }
            @media (min-width: 1600px) {
            }
            @media (min-width: 1920px) {
                margin-right: auto;
                margin-left: rem(360);
            }
            svg {
                font-size: rem(25);
                @media (min-width: 768px) {
                    font-size: rem(30);
                }
                @media (min-width: 1600px) {
                    font-size: rem(40);
                }
            }
        }
        &-size {
            margin: 0 rem(15) 0 rem(30);
            font-size: rem(15);
            font-weight: 400;
            line-height: rem(18);
            text-transform: uppercase;
            color: $grey_light;

            @media (min-width: 768px) {
                margin: 0;
                margin-left: rem(25);
                font-size: rem(17);
                line-height: rem(20);
            }
            @media (min-width: 1600px) {
                font-size: rem(21);
                line-height: rem(25);
            }
            @media (min-width: 1920px) {
                margin-left: rem(30);
            }
        }
    }
}
