.s-social{
    margin-bottom: rem(-10);
    @media (min-width: 768px) {
        margin-bottom: rem(10);
    }
    @media (min-width: 1024px) {
        margin-bottom: rem(90);
    }
    @media (min-width: 1600px) {
        margin-bottom: rem(97);
    }
    @media (min-width: 1920px) {
        margin-bottom: rem(80);
    }
    &__inner {
        max-width: rem(995);
        margin: 0 auto;

        @media (min-width: 1920px) {
            max-width: 100%;
        }
    }
    &__phone{
        display: flex;
        padding-bottom: rem(37);
        border-bottom: 1px solid #bdbec599;
        @media (min-width: 768px) {
            display: none;
        }
        &-tel{
            font-weight: 600;
            font-size: rem(19);
            line-height: rem(22);
            display: flex;
            align-items: center;

            color: $grey_light_more; 
            margin-right: rem(37);
        }
        &-btn{
            background: #424346;
            border-radius: 5px;

            font-weight: 400;
            font-size: rem(12);
            line-height: rem(14);
            color: $grey_light_more_2; 

            padding: rem(8) rem(14);
        }
    }
    &__send{
        display: flex;

        margin-top: rem(20);
        border-bottom: 1px solid #bdbec599;
        margin-bottom: rem(55);
        padding-bottom: rem(50);
        @media (min-width: 768px) {
            margin-bottom: rem(80);
            padding-bottom: 0;
            border-bottom: none;
        }
        @media (min-width: 1024px) {
            margin-bottom: rem(135);
            justify-content: space-between;
        }
        @media (min-width: 1600px) {
            margin-bottom: rem(155);
        }
        @media (min-width: 1920px) {
            margin-bottom: rem(170);
        }
        &-item{
            font-size: rem(13);
            @media (min-width: 768px) {
                font-size: rem(17);
                margin: 0 0 0 rem(61);
            }
            @media (min-width: 1024px) {
                font-size: rem(19);
                margin: 0 0 0 rem(80);
            }
            @media (min-width: 1600px) {
                font-size: rem(25);
                margin: 0 0 0 rem(52);
            }
            @media (min-width: 1920px) {
                font-size: rem(25);
                margin: 0 0 0 rem(170);
                padding: rem(14) 0;
            }
        }
        .phone{
            display: none;
            box-sizing: content-box;
            @media (min-width: 768px) {
                // min-width: rem(132);
                display: block;
                white-space: nowrap;
                order: 0;
                border-right: 1px solid #bdbec599;
                padding-right: rem(53);
                margin: 0;
            }
            @media (min-width: 1024px) {
                padding-right: rem(80);
            }
            @media (min-width: 1600px) {
                padding-right: rem(51);
            }
            @media (min-width: 1920px) {
                min-width: rem(250);
                padding-right: rem(171);
                .s-social__item-text{
                    font-size: rem(32);
                    line-height: rem(38);
                }
            }
        }
        .support{
            margin-right: rem(35);
            @media (min-width: 768px) {
                min-width: rem(195);
                order: 3;
                border-left: 1px solid #bdbec599;
                padding-left: rem(53);
                margin: 0;
            }
            @media (min-width: 1024px) {
                padding-left: rem(73);
            }
            @media (min-width: 1600px) {
                padding-left: rem(52);
            }
        }
        .mail{
            @media (min-width: 768px) {
                min-width: rem(172);
                order: 2;
            }
        }
    }
    &__item{
        &-text{
            display: block;
            font-weight: 400;
            font-size: rem(13);
            line-height: rem(15);
            color: $grey_light_more_2; 
            margin-bottom: rem(14);
            @media (min-width: 768px) {
                min-height: rem(48);
                font-size: rem(17);
                line-height: rem(20);
                margin-bottom: rem(20);
            }
            @media (min-width: 1024px) {
                min-height: rem(44);
                font-size: rem(19);
                line-height: rem(22);
                margin-bottom: rem(25);
            }
            @media (min-width: 1600px) {
                font-size: rem(25);
                line-height: rem(29);
                margin-bottom: rem(18);
            }
            @media (min-width: 1920px) {
                min-height: rem(58);
            }
        }
        &-btn{

        }
    }
    &__link{
        display: flex;
        justify-content: center;
        gap: rem(21);
        @media (min-width: 768px) {
            gap: rem(32);
        }
        @media (min-width: 1024px) {
            gap: rem(39);
        }
        @media (min-width: 1600px) {
            gap: rem(42);
        }
        &-item{
            img{
                width: rem(30);
                @media (min-width: 768px) {
                    width: rem(45);
                }
                @media (min-width: 1024px) {
                    width: rem(55);
                }
                @media (min-width: 1600px) {
                    width: rem(59);
                }
            }
        }


    }
    .btn-social{
        display: inline-block;
        background: #424346;
        border-radius: 5px;

        font-weight: 400;
        font-size: rem(12);
        line-height: rem(14);
        color: $grey_light_more_2; 

        padding: rem(8) rem(14);
        @media (min-width: 768px) {
            padding: rem(6) rem(18);
        }
    }
}

